.cardList {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 14px;
}

.cardToDo {
  padding: 14px 24px;

  display: flex;
  flex-direction: column;

  width: 334px;
  height: 154px;

  border-radius: 8px;
  background-color: var(--card-bg);
}

.titleCard {
  margin-bottom: 8px;
  font-family: inherit;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;

  color: var(--primary-text-color);
}

.textCard {
  flex: 1;

  margin-bottom: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  // height: 38px;
  max-height: 38px;


  font-family: inherit;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;

  color: var(--secondary-text-color);
}

.iconToDo {
  margin-top: auto;
  display: flex;
  gap: 8px;
}

.line {
  width: 100%;
  height: 1px;
  background-color: var(--border-card);
}

.bottomMenuCard {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
  margin-top: 14px;
}

.titleBottomMenuCard {
  font-family: inherit;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.5;

  color: var(--secondary-text-color);
}

.discriptionBottomMenuCard {
  font-family: inherit;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.5;

  color: var(--primary-text-color);
}

.textBottomMenuCard {
  display: flex;
  gap: 14px;
}
