@import '../../sass/utils/variables';
@import '../../sass/utils/mixin';

$transition: 300ms linear;

.backdrop {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  //   z-index: 1200;
}

.modalContainer {
  position: absolute;
  padding: 24px;
  background-color: var(--modal-background);
  border: 1px solid rgba(190, 219, 176, 0.5);
  border-radius: 8px;
  @media screen and (min-width: 320px) and (max-width: 374px) {
    width: 100%;
  }
  @include mobile {
    width: 335px;
  }

  @include tablet {
    top: 50%;
    left: 50%;
    max-width: 350px;
    width: 100%;
    transform: scale(1) translate(-50%, -50%);
  }
}

.title {
  margin-bottom: 24px;
}
.close {
  position: absolute;
  top: 18px;
  right: 18px;
  fill: var(--primary-text-color);
  background-color: var(--modal-background);
  padding: 0;
  border: none;
}
.buttonModal {
  @include font(500, 14px, 1.3);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  height: 49px;
  border-radius: 8px;
  background-color: var(--btn-bg);
  // color: var(--primary-text-color);
  border: none;
  stroke: var(--btn-modal-icon-plus);
  color: var(--btn-modal-bg);
  
  &:hover,
  &:focus {
    opacity: 0.9;
    // background-color: #89a779;
  }
}

.iconContainer{
  margin-right: 8px;
  width: 32px;
  height: 32px;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  
  background-color: var(--btn-modal-bg);
  stroke: var(--btn-modal-icon-plus);

  border-radius: 8px;
}